@import "../../node_modules/swiper/swiper-bundle.css";
@font-face {
  font-family: "MullerBlack";
  src: url("../fonts/MullerBlack.woff2") format("woff2"), url("../MullerBlack.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "MullerRegular";
  src: url("../fonts/MullerRegular.woff2") format("woff2"), url("../MullerRegular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "MullerBold";
  src: url("../fonts/MullerBold.woff2") format("woff2"), url("../MullerBold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
.footer__btn, .quiz__item-text, .advantages__list-item span, .karaoke-slider__pagination, .reservation-btn {
  font-family: "MullerBold";
  font-weight: 700;
}

.right-box__text, .advantages__text {
  font-family: "MullerBlack";
  font-weight: 800;
}

.container {
  max-width: 1900px;
  padding: 0 40px;
  margin: 0 auto;
}

body {
  font-family: "MullerRegular";
  color: #fff;
  font-size: 16px;
  line-height: 24px;
}

.title {
  font-size: 75px;
  font-family: "MullerBlack";
  text-transform: uppercase;
  display: inline;
  letter-spacing: 2px;
  line-height: 89px;
}

.reservation-btn {
  width: 275px;
  height: 70px;
  display: flex;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  color: #252121;
  border-radius: 30px;
  border: none;
  background: linear-gradient(0.45turn, #EA422A, #F69020, #FFC619);
}

.swiper-button {
  color: transparent;
  position: static;
  width: 70px;
  cursor: pointer;
  height: 70px;
  position: relative;
  border-radius: 50%;
  background-color: #2E3441;
  display: block;
}
.swiper-button::after {
  content: "";
  width: 18px;
  height: 18px;
  top: 25px;
  right: 23px;
}

.swiper-button-next,
.reservation-button-next {
  margin-left: 30px;
}
.swiper-button-next::after,
.reservation-button-next::after {
  margin-left: 30px;
  position: absolute;
  background-repeat: no-repeat;
  background-image: url("../img/right-arrow.svg");
}

.swiper-button-prev,
.reservation-button-prev {
  margin-right: 30px;
}
.swiper-button-prev::after,
.reservation-button-prev::after {
  position: absolute;
  background-image: url("../img/left-arrow.svg");
  background-repeat: no-repeat;
}

.subtitle {
  font-size: 24px;
  text-transform: uppercase;
}

.contacts-item {
  padding-left: 77px;
  position: relative;
  font-size: 15px;
}
.contacts-item::before {
  content: "";
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #373A40;
  position: absolute;
  left: 0;
  top: -25px;
}
.contacts-item::after {
  content: "";
  position: absolute;
}

.contact-tel::after {
  left: 16px;
  top: -12px;
  width: 29px;
  height: 29px;
  background-image: url("../img/phone.svg");
}

.contact-place::after {
  width: 34px;
  height: 34px;
  left: 13px;
  top: -13px;
  background-image: url("../img/map-icon.svg");
}

.contact-schedule::after {
  width: 28px;
  left: 18px;
  top: -8px;
  height: 28px;
  background-image: url("../img/calendar.svg");
}

.swiper {
  width: 100%;
  height: 100%;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

.start {
  background-blend-mode: normal;
  background-image: url("../img/bg.png");
  background-position: center;
  background-color: #191B20;
  background-repeat: no-repeat;
}

.start-page {
  position: relative;
  padding-top: 130px;
}

.header {
  padding-top: 45px;
}
.header__menu-btn {
  display: none;
  width: 29px;
  position: absolute;
  height: 12px;
  right: 29px;
  top: 70px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
.header__menu-btn::after {
  transition: all 0.3s;
  position: absolute;
  content: "";
  width: 29px;
  height: 3px;
  border-radius: 2px;
  top: 0;
  background-color: #F69020;
}
.header__menu-btn::before {
  transition: all 0.3s;
  position: absolute;
  content: "";
  width: 29px;
  height: 3px;
  border-radius: 2px;
  bottom: 0;
  background-color: #F69020;
}
.header__menu-btn--active::after {
  transform: rotate(45deg);
  top: 4px;
}
.header__menu-btn--active::before {
  transform: rotate(-45deg);
  top: 4px;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__nav-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.header__contacts {
  margin-left: 440px;
  z-index: 5;
  padding-top: 15px;
}
.header__contact-place {
  margin-left: 90px;
  color: #F69020;
}
.header__nav {
  display: flex;
  padding-top: 5px;
  z-index: 5;
  padding-right: 20px;
}
.header__nav-btn {
  width: 204px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-size: 13px;
  transition: all 0.3s;
  font-family: "MullerBold";
  border: 1px solid #393C43;
  border-radius: 30px;
}
.header__nav-btn:hover {
  background-color: #fff;
  color: #191B20;
}
.header__nav-btn + .header__nav-btn {
  margin-left: 15px;
}

.start__wrapper {
  display: flex;
  justify-content: space-between;
  padding-left: 12px;
}

.start-info {
  width: 887px;
  margin-right: 65px;
}
.start-info__subtitle {
  font-size: 41px;
  padding-left: 12px;
  text-transform: uppercase;
  margin-bottom: 40px;
  letter-spacing: 1px;
}
.start-info__sub-box {
  margin-bottom: 50px;
}
.start-info__sub-box span {
  font-size: 15px;
  height: 27px;
  width: 113px;
  margin: auto;
  vertical-align: middle;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  background-color: #343A3E;
  border-radius: 20px;
  margin-bottom: 45px;
}
.start-info__list {
  display: flex;
  justify-content: space-between;
}
.start-info ul.start-info__list {
  margin-bottom: 73px;
}
.start-info__item {
  width: 250px;
  padding-left: 65px;
  font-size: 18px;
  line-height: 24px;
  position: relative;
}
.start-info__item::before {
  content: "";
  position: absolute;
  width: 50px;
  left: 0;
  top: -3px;
  height: 50px;
  background-image: url("../img/list-mark.svg");
}
.start-info__item + .start-info__item {
  margin-left: 5px;
}
.start-info__imgbox {
  position: relative;
  width: 850px;
  overflow: hidden;
  margin-top: -275px;
  position: relative;
  height: 850px;
}
.start-info__img {
  position: absolute;
  top: 0;
  right: 114px;
  object-fit: cover;
}
.start-info__tag {
  position: absolute;
  width: 373px;
  bottom: 30px;
  left: -50px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 330px;
  background-image: url("../img/tag.png");
}
.start-info__tag img {
  filter: drop-shadow(0px 17px 52px rgba(0, 0, 0, 0.58));
}
.start-info__tag::before {
  width: 48px;
  height: 48px;
  position: absolute;
  content: "";
  top: 30px;
  transform: translateX(-50%);
  left: 50%;
  background-image: url("../img/24h.svg");
}
.start-info__tag-sub {
  margin-bottom: 10px;
  font-family: "MullerBlack";
  font-size: 16px;
}
.start-info__tag-main {
  display: block;
  font-size: 28px;
  font-family: "MullerBlack";
  color: #FFC619;
}

.start__telegram {
  display: flex;
  right: 0;
  position: absolute;
  right: 50px;
  flex-direction: column;
  align-self: center;
  height: 175px;
  justify-content: space-between;
  align-items: center;
}
.start__telegram-imgbox {
  width: 83px;
  height: 83px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: linear-gradient(-1.35turn, #EA422A, #F69020, #FFC619);
}
.start__telegram-text {
  transform: rotate(-90deg);
  width: 140px;
  display: inline;
  letter-spacing: 4px;
  color: #F69020;
  font-size: 9px;
}

.start-bottom {
  display: flex;
}
.start-bottom__item {
  width: 480px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #282B32;
  padding: 44px 81px 44px 40px;
}
.start-bottom__item:nth-child(even) {
  background-color: #202124;
}
.start-bottom__text {
  padding-left: 90px;
}

.karaoke {
  background-color: #131517;
  background-image: url("../img/halls-bg.png");
  background-repeat: no-repeat;
}
.karaoke__inner {
  display: flex;
}
.karaoke__title {
  margin-bottom: 35px;
  font-size: 62px;
  padding-left: 15px;
  text-align: left;
  display: block;
}
.karaoke__subtitle {
  padding-left: 15px;
  font-size: 24px;
  line-height: 36px;
  margin-bottom: 76px;
  margin-top: 35px;
  text-transform: uppercase;
}
.karaoke__left-box {
  padding-top: 110px;
  width: 432px;
  margin-right: 30px;
  margin-bottom: 30px;
}
.karaoke__item {
  margin-bottom: 78px;
}
.karaoke__item-element {
  background: linear-gradient(91.17deg, rgba(246, 144, 32, 0.24) -0.24%, rgba(246, 144, 32, 0) 97.99%);
  width: fit-content;
  font-size: 18px;
  padding: 11px 85px 10px 44px;
  position: relative;
}
.karaoke__item-element::before {
  content: "";
  width: 21px;
  height: 21px;
  position: absolute;
  left: 10px;
  background-repeat: no-repeat;
}
.karaoke__item-num::before {
  background-image: url("../img/room-icon.svg");
}
.karaoke__item-square::before {
  background-image: url("../img/expand-icon.svg");
}
.karaoke__item-capacity::before {
  background-image: url("../img/people-icon.svg");
}
.karaoke__item-element + .karaoke__item-element {
  margin-top: 10px;
}
.karaoke__gallery {
  height: 100%;
  width: 100%;
}
.karaoke__btn {
  border-radius: 40px;
  margin-left: 15px;
}
.karaoke__items {
  margin-bottom: 78px;
  margin-left: 15px;
}

.karaoke-slider {
  width: 432px;
  height: auto;
  margin-left: 15px;
  margin-bottom: 60px;
}
.karaoke-slider__pagination {
  padding-left: 15px;
  position: static;
  display: flex;
  font-size: 15px;
  letter-spacing: 2px;
  text-align: left;
  margin-bottom: 35px;
}
.karaoke-slider .swiper-pagination-bullet {
  margin-right: 35px;
  text-decoration: none;
  height: 100%;
  width: fit-content;
  background: transparent;
}
.karaoke-slider .swiper-pagination-bullet-active {
  color: #FFC619;
}
.karaoke-slider .swiper-button-wrapper {
  padding-left: 15px;
  display: flex;
  justify-content: space-around;
  width: 260px;
}
.karaoke-slider .swiper-wrapper {
  height: auto;
}
.karaoke-slider .swiper-counter-all {
  font-size: 16px;
  color: #606571;
}
.karaoke-slider .swiper-counter {
  color: #fff;
  font-size: 22px;
}

.gallery .gallery-thumbs__items {
  height: 100%;
  width: 100%;
}
.gallery .gallery-thumbs__items .swiper-slide {
  transition: all 0.3s;
  width: 25%;
  height: 100%;
  opacity: 0.4;
  cursor: pointer;
}
.gallery .gallery-thumbs__items .swiper-slide:hover {
  opacity: 1;
}
.gallery .gallery-thumbs__items .swiper-slide-thumb-active {
  transition: all 0.3s;
  opacity: 1;
}

.gallery {
  position: relative;
  width: 100%;
  float: right;
  overflow: hidden;
}

.gallery-top__item {
  opacity: 0.6;
}
.gallery-top__item img {
  object-fit: cover;
  width: 100%;
  height: 832px;
}

.gallery-thumbs {
  width: 100%;
}
.gallery-thumbs__item:not(.swiper-slide-thumb-active) {
  position: relative;
}
.gallery-thumbs__item:not(.swiper-slide-thumb-active)::after {
  content: "";
  transition: all 0.2s;
  position: absolute;
  z-index: 5;
  top: 50%;
  left: 50%;
  width: 50px;
  transform: translate(-50%, -50%);
  visibility: hidden;
  height: 50px;
  background-image: url("../img/eye.svg");
}
.gallery-thumbs__item:not(.swiper-slide-thumb-active):hover .gallery-thumbs__overlay {
  visibility: visible;
}
.gallery-thumbs__item:not(.swiper-slide-thumb-active):hover::after {
  visibility: visible;
}
.gallery-thumbs__overlay {
  transition: all 0.2s;
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(255, 198, 25, 0.4);
  visibility: hidden;
}

.reservation-top {
  display: flex;
  margin-top: 13px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 35px;
}

.reservation-step {
  position: relative;
}
.reservation-step__num {
  z-index: 20;
  visibility: hidden;
  position: absolute;
  width: 61px;
  height: 61px;
  box-shadow: 0px 12px 12px rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  background-color: #F69020;
  display: flex;
  justify-content: center;
  align-items: center;
  top: -20px;
  right: -10px;
}
.reservation-step__info {
  margin-top: 35px;
  display: flex;
  align-items: center;
}
.reservation-step__info-imgbox {
  margin-right: 25px;
}

.reservation {
  background-color: #131517;
  background-image: url("../img/reserve-bg.png");
  background-blend-mode: normal;
  padding-top: 110px;
  padding-bottom: 95px;
}
.reservation__subtitle {
  font-size: 24px;
  text-transform: uppercase;
}
.reservation .swiper {
  padding-top: 20px;
}
.reservation .swiper-button-wrapper {
  width: 250px;
  display: flex;
  justify-content: space-between;
  height: 100%;
  align-items: center;
}
.reservation .swiper-pagination-fraction {
  position: static;
  width: auto;
}
.reservation .swiper-slide {
  opacity: 0.5;
  overflow: visible;
}
.reservation .swiper-slide-active {
  opacity: 1;
}
.reservation .swiper-slide-active .reservation-step__num {
  visibility: visible;
}
.reservation .swiper-slide-active .reservation-step__imgbox {
  position: relative;
}
.reservation .swiper-slide-active .reservation-step__imgbox::after {
  background-size: contain;
  background-repeat: no-repeat;
  content: "";
  position: absolute;
  right: -14px;
  top: 0px;
  left: -14px;
  bottom: -2px;
  background-image: url("../img/frame.svg");
}
.reservation .swiper-slide-prev,
.reservation .swiper-slide-next {
  opacity: 0.9;
}

.advantages {
  background-image: url("../img/achievements-bg.png");
  padding-top: 97px;
  padding-bottom: 110px;
  background-color: #191B20;
}
.advantages__inner {
  display: flex;
  justify-content: space-between;
}
.advantages__subtitle {
  margin-top: 33px;
  margin-bottom: 45px;
}
.advantages__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
}
.advantages__item {
  background-color: #202124;
  padding: 50px 30px 35px 50px;
}
.advantages__item:nth-child(odd) {
  background-color: #282B32;
}
.advantages__img {
  margin-bottom: 30px;
  height: 86px;
}
.advantages__text {
  font-size: 18px;
  color: #fff;
  margin-bottom: 16px;
}
.advantages__list-item {
  position: relative;
  padding-left: 26px;
}
.advantages__list-item span {
  color: #F69020;
}
.advantages__list-item::before {
  content: "";
  position: absolute;
  left: 0;
  top: 3px;
  width: 16px;
  height: 16px;
  background-image: url("../img/advantages/checked.svg");
}
.advantages__right-box {
  background-color: #191B20;
  background-image: url("../img/advantages/wall-bg.png");
  background-blend-mode: lighten;
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}
.advantages__img-box {
  width: 450px;
  height: 685px;
  margin-bottom: 45px;
}
.advantages__btn {
  margin: 0 auto;
}
.advantages__title {
  position: relative;
  z-index: 5;
}

.box__img {
  margin-top: -175px;
  height: 100%;
  object-fit: cover;
}

.quiz {
  background-image: url("../img/quiz-bg.png");
  background-color: #191B20;
  padding-top: 120px;
  padding-bottom: 130px;
}
.quiz__subtitle {
  margin-top: 33px;
  margin-bottom: 45px;
}
.quiz__inner {
  display: flex;
}
.quiz__list {
  max-width: 1200px;
}
.quiz__item {
  width: 100%;
  margin-bottom: 20px;
  cursor: pointer;
}
.quiz__item-panel {
  transition: all 0.2s;
  background-color: #282B32;
  padding: 20px 20px 17px 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.quiz__item-panel:hover {
  background-color: #343740;
}
.quiz__item-arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 43px;
  background-repeat: no-repeat;
  height: 43px;
  box-shadow: 0px 9px 16px rgba(0, 0, 0, 0.21);
}
.quiz__item-arrow svg {
  transition: all 0.5s;
}
.quiz__item-info {
  transition: max-height ease-in-out 0.3s;
  max-height: 0;
  padding-top: 0;
  line-height: 31px;
  overflow: hidden;
}
.quiz__item-text {
  font-size: 18px;
}
.quiz__item--active .quiz__item-panel {
  margin-bottom: 24px;
}
.quiz__item--active .quiz__item-info {
  max-height: 1000px;
}
.quiz__item--active .quiz__item-arrow svg {
  transform: rotate(-180deg);
}
.quiz__item--active .quiz__item-arrow svg path {
  fill: #606571;
}

.right-box {
  background-image: url("../img/advantages/wall-bg.png");
  background-blend-mode: lighten;
  background-color: #191B20;
  background-position: center;
  margin-left: 77px;
}
.right-box__text {
  font-size: 32px;
  margin-bottom: 46px;
  line-height: 52px;
  width: 540px;
}
.right-box__text span {
  color: #F69020;
}
.right-box__img {
  margin-bottom: 63px;
}
.right-box__btn {
  margin: 0 auto;
  border-radius: 40px;
}

.footer {
  background-color: #131517;
  padding: 93px 0 91px;
  background-image: url("../img/footer-bg.png");
}
.footer__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer__btn {
  width: 232px;
  border: 1px solid #F69020;
  text-transform: uppercase;
  background: transparent;
  color: #fff;
  font-size: 13px;
  padding: 25px 0 18px;
  border-radius: 40px;
  cursor: pointer;
}
.footer .contacts-item {
  margin-left: 30px;
}
.footer .contacts-item::before {
  top: -10px;
}
.footer .contacts-item::after {
  top: 3px;
}

.scroll {
  margin-top: -40px;
  transform: rotate(-90deg);
  display: flex;
  width: 250px;
  align-self: flex-start;
  flex-direction: row-reverse;
  justify-content: space-around;
  align-items: center;
}
.scroll__link {
  width: 70px;
  height: 70px;
  background-color: #2E3441;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transform: rotate(90deg);
}
.scroll__text {
  font-size: 12px;
  color: #606571;
  text-transform: uppercase;
  letter-spacing: 3px;
}

@media (max-width: 1815px) {
  .start-info__img {
    right: 70px;
  }

  .contacts {
    margin-left: 240px;
